body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
.centerit{
	margin: auto;
    width: 50%;
    padding: 10px;
}
.stopButton{
	display:block;
    margin:auto;
	background-image: url( './stop-button.png' );
    height: 128px;  
    width: 128px;
    cursor: pointer;
}
.startButton{
	display:block;
    margin:auto;
	background-image: url( './record-button.png' );
    height: 128px;  
    width: 128px;
    cursor: pointer;
}
.selectbot{
    margin-top: 20px;
    margin-right: 10px;
    text-align: center;
}

.btn{
width:260px;
height:50px;
margin-left:45px
}
.btn1{
width:260px;
height:85px;
margin-left:42px
}
.button4{
color:#fff;
padding:20px 100px;
border:none;
outline:none;
font-size:20px;
text-shadow:-2px 2px 0 rgba(0,0,0,0.2);
font-weight:700;
-webkit-transition:all .1s linear;
-o-transition:all .1s linear;
transition:all .1s linear;
background:-webkit-gradient(linear,left top, left bottom,from(#977d60),to(#685642));
background:-webkit-linear-gradient(top,#977d60,#685642);
background:-o-linear-gradient(top,#977d60,#685642);
background:linear-gradient(to bottom,#977d60,#685642);
-webkit-box-shadow:-1px 0 1px #584938,0 1px 1px #584938,-2px 1px 1px #584938,-1px 2px 1px #584938,-3px 2px 1px #584938,-2px 3px 1px #584938,-4px 3px 1px #584938,-3px 4px 1px #493c2e,-5px 4px 1px #493c2e,-4px 5px 1px #493c2e,-6px 5px 1px #493c2e;
        box-shadow:-1px 0 1px #584938,0 1px 1px #584938,-2px 1px 1px #584938,-1px 2px 1px #584938,-3px 2px 1px #584938,-2px 3px 1px #584938,-4px 3px 1px #584938,-3px 4px 1px #493c2e,-5px 4px 1px #493c2e,-4px 5px 1px #493c2e,-6px 5px 1px #493c2e
}
.button4:active{
-webkit-box-shadow:none;
        box-shadow:none;
-ms-transform:translate3d(-6px,6px,0);
-webkit-transform:translate3d(-6px,6px,0);
transform:translate3d(-6px,6px,0)
}
.button5{
width:250px;
height:45px;
border:none;
outline:none;
-webkit-box-shadow:-4px 4px 5px 0 #feb361;
        box-shadow:-4px 4px 5px 0 #feb361;
color:#fff;
font-size:14px;
text-shadow:0 1px rgba(0,0,0,0.4);
background-color:#FE9A2E;
border-radius:3px;
font-weight:700
}
.button5:hover{
background-color:#FF8000;
cursor:pointer
}
.button5:active{
margin-left:-4px;
margin-bottom:-4px;
padding-top:2px;
-webkit-box-shadow:none;
        box-shadow:none
}
.button6{
width:250px;
height:45px;
border:none;
outline:none;
-webkit-box-shadow:-5px 5px 5px 0 #8aa7fb;
        box-shadow:-5px 5px 5px 0 #8aa7fb;
margin-top:20px;
color:#fff;
font-size:14px;
text-shadow:0 1px rgba(0,0,0,0.4);
background-color:#5882FA;
border-radius:3px;
font-weight:700
}
.button6:hover{
background-color:#2E64FE;
cursor:pointer
}
.button6:active{
margin-left:-5px;
margin-bottom:-5px;
padding-top:2px;
-webkit-box-shadow:none;
        box-shadow:none
}
.bckg{
background-image: url("./image.jpg");
background-position: center;
background-repeat: no-repeat;
background-size: cover;
height: 700px;
color: white;
}
.margint5{
  margin-top: 100px;
}
.md5{
  margin-bottom: 100px;
}


